import React from 'react';
import Image from '../../assets/images/baner_prefabrykacja.jpg';

class About extends React.Component {
  render() {
    return (
      <div className="container">
        <div className="content">
          <div
            className="content-img"
            style={{
              backgroundImage: `url(${Image})`,
              backgroundSize: `cover`,
              display: `flex`,
              flexDirection: `column`,
              height: `50vh`,
              alignItems: `center`,
              justifyContent: `center`,
              marginTop: `40vh`,
            }}
          >
            <h2 style={{ fontSize: `3rem`, marginTop: `-60vh` }}>O nas</h2>
          </div>
          <div className="content-header">
            <p>
              Jesteśmy polską firmą inżynierską z 10-letnim doświadczeniem w
              branży automatyki przemysłowej.
            </p>
            <p>
              Zajmujemy się kompleksową obsługą firm produkcyjnych i
              instalacyjnych w zakresie projektowania, produkcji oraz dostaw
              akcesoriów elektrotechnicznych.
            </p>
            <p>
              ## Nasze atuty
              <li>fachowa wiedza</li>
              <li>szybki czas reakcji na zapytania naszych Klientów,</li>
              <li>
                dostosowanie oferty produktowej do indywidualnych potrzeb
                obsługiwanych firm
              </li>
            </p>
            <p>
              Klienci **ProfiSerwis** przez 7 dni w tygodniu mają do dyspozycji
              swojego doradcę, który pomaga im rozwiązywać codzienne problemy
              związane z utrzymaniem ruchu, rozbudową produkcji oraz realizacją
              inwestycji.
            </p>
            <p>### _Działamy na terenie całego kraju_ ##</p>
            <p>
              Główne obszary naszej działalności to:
              <li>Automatyka przemysłowa</li>
              <li>Robotyka</li>
              <li>Elektroenergetyka</li>
              <li>Czysta energia</li>
            </p>
            <p>
              Do grona naszych Klientów należą zakłady przemysłowe, firmy
              budowlane oraz instalatorzy, którzy cenią w naszych usługach
              specjalistyczną wiedzę, profesjonalne i kompleksowe podejście do
              powierzonych nam zadań oraz dużą elastyczność.
            </p>
          </div>
        </div>
      </div>
    );
  }
}

export default About;
